import React from 'react'

const Progress_bar = ({bgcolor,progress,height}) => {
	
	const Parentdiv = {
		height: 5,
		width: '100%',
		backgroundColor: '#D9D9D9',
		borderRadius: 40,
		margin: 1
	}
	
	const Childdiv = {
		height: '100%',
		width: `${progress}%`,
		backgroundColor: bgcolor,
	borderRadius:40,
		textAlign: 'right'
	}
	
	const progresstext = {
		padding: 10,
		color: 'null',
		fontWeight: 400
	}
		
	return (
	<div style={Parentdiv}>
	<div style={Childdiv}>
		
	</div>
	</div>
	)
}

export default Progress_bar;
